.article.page {
  display: flex;
  flex-direction: column; }
  .article.page > .title-bar {
    border-bottom: 1px solid;
    border-bottom-color: #42afe3;
    border-bottom-color: var(--color-primary);
    display: flex;
    flex-direction: row;
    align-items: center; }
    .article.page > .title-bar > .title {
      align-items: center;
      display: flex;
      flex: 1;
      font-size: 2em;
      font-weight: bold;
      justify-content: flex-start;
      margin: 0;
      min-height: 40px;
      padding: 10px; }
    .article.page > .title-bar > .toggle.off {
      color: rgba(153, 153, 153, 0.5); }
    .article.page > .title-bar > .toggle.secret.on {
      color: #97cd76;
      stroke: #4b7d2d;
      stroke-width: 2px; }
    .article.page > .title-bar > .toggle.favorite.on {
      stroke: #998100;
      stroke-width: 3px; }
    .article.page > .title-bar > .redirected-from {
      font-size: .7rem;
      padding: .2rem .5rem;
      opacity: .6; }
  .article.page > .tab-set .tab-contents:not(.read) {
    padding: 0; }
  .article.page > .tab-set .tab-contents > p {
    align-items: center;
    display: flex;
    flex: 1;
    justify-content: center; }
  .article.page > .tab-set > .tabs.read ~ .tab-contents {
    padding: .5rem; }
  .article.page > .tab-set > .tabs.html > .tab.html.is-active {
    background-color: #fff;
    border-bottom-color: #fff; }
  .article.page .tag-bar.tags {
    border-top: 1px solid var(--color-primary); }
    .article.page .tag-bar.tags:not(.readOnly) {
      background: #e9e9e9;
      box-shadow: 0 3px 3px rgba(211, 214, 219, 0.75) inset;
      padding-top: 3px; }
