.tab-set {
  background-color: #e9e9e9;
  box-shadow: 0 3px 3px rgba(211, 214, 219, 0.75) inset;
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 0;
  max-width: 100vw;
  padding: 0; }
  .tab-set ul.tabs {
    align-items: center;
    border-bottom: 1px solid;
    border-bottom: 1px solid var(--color-primary);
    display: flex;
    justify-content: flex-start;
    line-height: 1em;
    list-style: none;
    margin: 0;
    min-height: 40px;
    overflow: visible;
    padding: 5px 5px 0 5px;
    z-index: 1; }
    .tab-set ul.tabs > .tab {
