        border-bottom-color: var(--color-background);
        box-shadow: -3px -3px 3px rgba(211, 214, 219, 0.75), 3px -3px 3px rgba(211, 214, 219, 0.75);
        box-shadow: -3px -3px 3px var(--color-shadow), 3px -3px 3px var(--color-shadow);
        color: #333;
