.mce-content-body include {
  background-color: #fafafa;
  border: 1px dotted #d3d6db;
  border-radius: 3px;
  color: rgba(0, 0, 0, 0.25);
  display: block;
  margin: -3px;
  padding: 3px; }
  .mce-content-body include[contentEditable=false][data-mce-selected] {
    outline: none; }
  .mce-content-body include a {
    color: inherit;
    cursor: default;
    font-weight: bold; }
  .mce-content-body include:after {
    background-color: #fffefe;
    border-top: 1px solid #d3d6db;
    color: rgba(0, 0, 0, 0.5);
    content: "included from: " attr(from) ", sections: " attr(sections);
    display: block;
    margin: 3px -3px -3px -3px;
    padding: 0 5px;
    text-align: right; }
