.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.article-children {
  display: flex;
  flex-direction: column;
  margin-top: 2em;
  overflow: hidden; }
  .article-children header {
    align-items: center;
    border-bottom: 1px solid var(--color-primary);
    color: #333;
    display: flex;
    flex-direction: row;
    font-weight: bold;
    justify-content: flex-start;
    opacity: .9;
    padding: .5rem; }
    .article-children header > svg {
      margin-right: .25rem;
      padding: .1rem; }
  .article-children .link-list {
    columns: 4;
    column-fill: balance;
    column-gap: 10px; }
    .article-children .link-list > a {
      display: inherit; }

@media only screen and (min-width: 551px) and (max-width: 979px) {
  .article-children .link-list {
    columns: 3; } }

@media only screen and (max-width: 550px) {
  .article-children .link-list {
    columns: 2; }
    .article-children .link-list a {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap; } }
