.wiki.layout {
  background: #f6f6f6;
  background: var(--color-background);
  display: flex;
  flex: 1;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  max-height: 100vh;
  max-width: 100vw;
  min-height: 100vh;
  min-width: 100vw;
  padding: 0;
  width: 100vw; }
  .wiki.layout > .header .navigation.toggle {
    background: inherit;
    border-bottom-width: 2px;
    border: 1px solid transparent;
    color: inherit;
    cursor: pointer;
    display: none;
    margin-bottom: -4px;
    margin-left: .5rem;
    padding: .5rem; }
  .wiki.layout > .content {
    display: flex;
    flex-direction: row;
    flex: 1;
    position: relative;
    height: 100%;
    width: 100%; }
    .wiki.layout > .content > .navigation {
      width: 150px;
      z-index: 1; }
    .wiki.layout > .content > .page {
      flex: 1;
      max-height: calc(100vh - 44px);
      width: 100%; }
    .wiki.layout > .content > .navigation ~ .page {
      width: calc(100vw - 150px); }
  .wiki.layout.small {
    font-size: 10pt; }
    .wiki.layout.small > .header > .logo {
      min-width: auto; }
      .wiki.layout.small > .header > .logo > .nerd, .wiki.layout.small > .header > .logo > .rage {
        display: none; }
    .wiki.layout.small > .header > .navigation.toggle {
      display: flex; }
    .wiki.layout.small.collapse-navigation > .content > .navigation {
      display: none; }
    .wiki.layout.small.expand-navigation > .header > .navigation.toggle {
      background: var(--color-background);
      border-bottom-color: var(--color-background);
      box-shadow: 3px 10px 10px #00000055;
      color: var(--color-primary);
      margin-bottom: -6px; }
    .wiki.layout.small.expand-navigation .content > .navigation {
      bottom: 0;
      box-shadow: 3px 10px 10px #00000055;
      left: 0;
      position: absolute;
      top: 0;
      width: 85%;
      z-index: 5; }
    .wiki.layout.small button {
      font-size: 10pt; }
  .wiki.layout.no-campaign > .header {
    justify-content: flex-end; }
    .wiki.layout.no-campaign > .header > .logo {
      flex: 1; }
    .wiki.layout.no-campaign > .header > .navigation.toggle, .wiki.layout.no-campaign > .header > .search-box {
      display: none; }
