.tag {
  align-items: center;
  background-color: #f6f6f6;
  border-radius: 3px;
  border: 1px solid;
  display: flex;
  flex-direction: row;
  font-size: .9em;
  grid-gap: .25em;
  height: 2em;
  justify-content: space-between;
  margin: 3px;
  padding: 3px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none; }
  .tag > svg {
    padding: .1rem; }
    .tag > svg:first-child {
      margin-right: .25rem; }
    .tag > svg:last-child {
      margin-left: .25em; }
  .tag.is-link {
    color: #08f;
    font-size: .8rem; }
  .tag .remove {
    cursor: pointer; }
  .tag svg.remove {
    display: none; }
  .tag.removable > svg.remove {
    display: inline-block; }
