.tab-set > .tabs.wysiwyg ~ .tab-contents {
  padding: 0;
  overflow: inherit; }

.wysiwyg-editor {
  display: flex;
  flex-direction: column;
  height: 100%; }
  .wysiwyg-editor .menubar {
    box-shadow: 2px 2px 5px #d3d6db;
    box-shadow: 2px 2px 5px var(--color-shadow);
    background: #f6f6f6;
    background: var(--color-background);
    border-color: #42afe3;
    border-color: var(--color-primary);
    border-style: solid;
    border-width: 0 0 1px 0;
    display: grid;
    height: 35px;
    margin-bottom: 5px;
    overflow: hidden; }
    .wysiwyg-editor .menubar > .mce-tinymce {
      border-width: 0 !important;
      display: block !important; }
    .wysiwyg-editor .menubar .mce-btn.mce-active {
      background: var(--color-primary);
      color: var(--color-secondary); }
    .wysiwyg-editor .menubar button {
      background: transparent;
      border: none;
      border-radius: 0; }
  .wysiwyg-editor .mce-content-body {
    flex: 1;
    outline: none;
    overflow-y: auto;
    padding: 10px; }
  .wysiwyg-editor .mce-tooltip {
    font-family: inherit;
    font-family: var(--font-primary); }
